import React, { Component } from 'react';
import ema from '../assets/ema.png';

class Project extends Component {
  constructor(props) {
    super(props);
    this.state = {
      display: 'none'
    }
    this.showOverlay = this.showOverlay.bind(this);
    this.hideOverlay = this.hideOverlay.bind(this);
  }

  showOverlay() {
    this.setState({
      display: 'flex'
    });
  }

  hideOverlay() {
    this.setState({
      display: 'none'
    });
  }

  render() {
    const { skill } = this.props;
    let { display } = this.state;
    return (
      <div
        className="skill"
        onMouseOver={this.showOverlay}
        onMouseLeave={this.hideOverlay}
      >
        <div className="overlay animate-skill" style={{ display }} >
          <span className="proficiency tech-container">
            {/* <span className="title">Technologies: &nbsp;</span> */}
            <span className="techs">
              {
                skill.technologies.join()
              }
            </span>
          </span>
          <span className="experience url">
            {/* <span className="title">URL: </span> */}
            <span className="value">
              <a href={skill.url} target="new">
                {skill.url}
              </a>
            </span>
          </span>
          <span className="experience url">
            {/* <span className="title">Repo: </span> */}
            <span className="value">
              <a href={skill.repo} target="new">
                {skill.repo}
              </a>
            </span>
          </span>
          <span className="projects">
            <span className="value">{skill.tagline}</span>
          </span>
        </div>
        <h1 className="project-logo">
          {
            skill.name[0].toUpperCase()
          }
        </h1>
        <div className="details">
          <span>{skill.name}</span>
        </div>
      </div>
    );
  }
};

export default Project;
